.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 406px;
  line-height: 14px;
}

.card__content {
  padding: 0 16px 16px;
}

.card__image {
  background-size: cover;
  width: 160px;
  height: 134px;
  margin: 32px 19px 38px;
}

.card__title {
  color: var(--main-font-color);
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: var(--secondary-font-color);
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
}

.card__row {
  justify-content: space-between;
  margin-top: 0;
  display: flex;
}

.card__row--last {
  margin: 24px 0 16px;
}

.card__reviews {
  text-align: right;
  color: var(--main-font-color);
  margin-top: 2px;
  font-size: 10px;
  font-weight: 400;
}

.card__price-title {
  color: var(--secondary-font-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price {
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover, .card:hover .card__button {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  list-style: none;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

:root {
  --font-color: #060b35;
  --secondary-font-color: #616070;
}

body, div, h1, p, ul, li, button {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.container {
  width: 90%;
  margin: 20px auto;
}

